*{
  margin: 0;
}
::-webkit-scrollbar {
  background-color: transparent;
  border-left: 1px solid black;
  width: 5px;

}
a{
  color:rgba(255, 255, 255, 0.8);
  text-decoration: none;
  transition: 0.3s ease all;
  
}
a:hover{
  color: white;
}
::-webkit-scrollbar-button {
  width: 0;
  height: 0;
}
::selection {
  color: rgb(42, 42, 42);
  background: rgba(208, 208, 8, 1);
}

@font-face {
  font-family: MontB;
  src: url(./img/Montserrat-SemiBold.ttf);
}
@font-face {
  font-family: Mont;
  src: url(./img/Montserrat-Thin.ttf);
}
@font-face {
  font-family: MontR;
  src: url(./img/Montserrat-Regular.ttf);
}
@font-face {
  font-family: PAS;
  src: url('./img/DIE_of_LIFE.ttf');
}
body{
  background-color: rgb(42, 42, 42);
  overflow-x: hidden;

}
  .app-wrapper{
    overflow-x: hidden;
    display: grid;
    // height: 1000vh;
    width: 100vw;
    grid-template-rows: 28px 1fr;
    grid-template-columns: 0.5fr 8fr 0.5fr;
    grid-gap: 5px;
    font-family: MontB;
    color: white;
    grid-template-areas:  
    "n c s"
    "n c s";
  
    
    &__nav{
      grid-area: n; 
      // border-right: 1px solid rgb(122, 122, 122);
      display: flex;
      flex-direction: column;
      justify-content: center;
      background-color: rgb(8, 8, 8);
      height: 100vh;
      position: fixed;
      padding-top: 23px;
      .logo{
        img{
          width: 45px;
          height: auto;
          position: absolute;
          left: 8px;
          top: 16px;
          opacity: 0.8;
          transition: 0.5s all ease;
          &:hover{
            opacity: 1;
            cursor: pointer;
          }
        }
      }
      
      .nav-container{
        &__href{
          display: flex;
          justify-content: start;
          align-items: center;
          box-sizing: border-box;
          padding: 11.5px ;
          font-size: 0px;
          
          &-img{
            cursor: pointer;
            width: 40px;
            opacity: 0.8;
            border: 2px solid rgba(255, 255, 255, 0);
            border-radius: 40%;
            transition: 0.5s all ease;
            &:hover{
            border: 2px solid rgba(255, 255, 255, 0.8);
            opacity: 1;

            }
          }
        }
      }
    }
    &__set{
      grid-area: s; 
      // border-left: 1px solid white;
    }
    &__content{
      grid-area: c;  
      .content{
        display: grid;
        width: 90vw;
        margin-left: 10px;
        grid-template-columns: 10fr 10fr 20fr;
        margin-top: -10px;
        grid-gap: 5px;
        grid-template-rows: 30vh 47vh 40vh;
        grid-template-areas: 
        
        "name name name "
        "ava des des"
        "my my new"  ;
      &-name{
        display: flex;
        justify-content: center;
        align-items: center;
        // width: 90vw;
        grid-area: name;
        margin: auto;
        // height: 30vh;
        background-image: url(./img/content.png);
        background-size: cover ;
        background-position: center;

        &__title{
          font-size: 80px;
          font-family: PAS;
          letter-spacing: 4px;
          background-color: rgba(8, 8, 8, 0.8);
          width: 90vw;
          justify-content: center;
          align-items: center;
          display: flex;
          height: 30vh;
        }

      } 
      .ava {
        &:hover{
          background-color: rgb(8, 8, 8);
        }
        transition: 0.3s ease all;
        background-color: rgba(8, 8, 8, 0.8);

        grid-area: ava;
        display: flex;
        justify-content: center;
        // max-width: 300px;
        align-items: center;
        img{
          width: 225px;
          border: 5px solid rgba(208, 208, 8, 1);
          
        }
      }
      .description-con{
        &:hover{
          background-color: rgb(8, 8, 8);
        }
        background-color: rgba(8, 8, 8, 0.8);
        transition: 0.3s ease all;
        grid-area: des;
        &__title{
          font-family: MontB;
          text-transform: uppercase;
          font-size: 30px;
          h2{
            margin: 10px 0 23px 0px !important;
            text-align: center;
            // color: rgba(208, 208, 8, 1);
          }
          
        }
        &__text{
          display: flex;
          ul{
            min-width: 300px;
            letter-spacing: 0px;
            font-size: 23px;
            display: flex;
            flex-wrap: wrap;
            font-family: MontR;
            li{
              min-width: 80px;
              margin: 0px 20px 20px 0;
              display: block;
            }
          }
          .description{
            border-left: 5px solid rgb(42, 42, 42);
            min-height: 300px;
            margin: 0 !important;
            padding: 12.5px;
            font-family: Mont;
            font-size: 14px;
            line-height: 30px;
            max-height: 300px;
            overflow: hidden;
            display: flex;
          }
        }
      }
      .myPosts{
        &:hover{
          background-color: rgb(8, 8, 8);
        }
        transition: 0.3s ease all;
        grid-area: my;
        background-color: rgba(8, 8, 8, 0.8);
        min-height: 50vh;
        .post{
          padding-left: 5px;
          min-height: 13vh;
          border-bottom: 2px solid rgb(42, 42, 42);
          .post-title{
            outline: none;
            border: 0px;
            margin: 10px 0 0 8px;
            width:25vw;
            background-color: rgba(0, 0, 0, 0);    
            height: 42px;
            color: white;
            font-size: 40px;
            font-family: PAS;
            text-transform: uppercase;
            border-bottom: 2px solid rgba(255, 255, 255, 0);
            cursor: text !important;
            transition: 0.3s all ease;
            letter-spacing: 2px;
            &:focus{
              border-bottom: 2px solid rgba(255, 255, 255, 0.5);
            }
          }
          &-message{
            outline: none;
            border: 0px;
            cursor: pointer;
            margin: 10px 0 0 8px;
            background-color: rgba(0, 0, 0, 0);
            min-height: 4vh;
            // position: absolute;
            color: white;
            max-width: 50vw;
            // margin-top: -35px;
            font-family: MontR;
            resize: none;
            border-bottom: 2px solid rgba(255, 255, 255, 0);
            &:focus{
            border-bottom: 2px solid rgba(255, 255, 255, 0.5);
            }
          }
        }
      }
      .newPosts{
        &:hover{
          background-color: rgb(8, 8, 8);
        }
        transition: 0.3s ease all;
        grid-area: new;
        background-color: rgba(8, 8, 8, 0.8);
        display: flex;
        flex-direction: column;
        padding: 10px;
        position: relative;
        &-title{
          outline: none;
          border: 0px;
          margin: 10px 0 0 5px;
          width:75%;
          background-color: rgba(0, 0, 0, 0);    
          height: 42px;
          color: white;
          font-size: 40px;
          font-family: PAS;
          text-transform: uppercase;
          border-bottom: 2px solid rgba(255, 255, 255, 0);
          transition: 0.3s all ease;
          letter-spacing: 2px;
          &:focus{
            border-bottom: 2px solid rgba(255, 255, 255, 0.5);
          }
        }
        &-input{
          outline: none;
          border: 0px;
          margin: 10px 0 0 5px;
          background-color: rgba(0, 0, 0, 0);
          height: 40vh;
          color: white;
          width: 75%;
          font-family: MontR;
          resize: none;
          border-bottom: 2px solid rgba(255, 255, 255, 0);
          &:focus{
          border-bottom: 2px solid rgba(255, 255, 255, 0.5);
          }
        }
        button{
          outline: none;
          border: 0px;
          width: 125px;
          background-color: rgba(0, 0, 0, 0);
          border: 2px solid rgba(255, 255, 255, 0);
          height: 46px;
          color: rgba(255, 255, 255, 0.733);
          font-family: MontR;
          text-transform:lowercase;
          font-size: 20px;
          position: absolute;
          bottom: 10px;
          right: 10px;
          letter-spacing: 8px;
          padding-left: 15px;
          border-bottom: 2px solid rgba(0, 0, 0, 0);
          transition: 0.5s all ease;
        }
        button:hover{
          border: 2px solid rgba(255, 255, 255, 0.5);
          cursor: pointer;
        }
      }
    }
    }
    &__set{
      background-color: rgba(8, 8, 8, 0.8);
      height: 915vh;
      // position: fixed;
      .friends{
        // white-space: pre-wrap;
        // width: 60px;
        border-bottom: 5px solid rgb(42, 42, 42);
        font-size: 60px;
        height: 70px;
        justify-content: center;
        align-items: center;
        display: flex;
        padding-top: 5px;
        font-family: PAS;
        // text-align: center;
        text-transform: uppercase;

      }
    }
    .messages{
      margin-left: -10px;
      // margin-top: 10px;
      .messages-con{
        display: grid;
        grid-template-columns: 4fr 8fr;
        position: relative;
        &__chats{
          // max-width: 30vw;
          .container{
            .find{
              background-color: rgb(8, 8, 8);
              margin-bottom: 5px;
              // margin-top: 5px;
              &-con{
                display: flex;
                align-items: center;
                height: 8vh;
                button{
                  border: 0px;outline: none;
                  background-color: transparent;

                  img{
                    width: 35px;
                    transition: 0.5s ease all;
                    border: 2px solid rgb(8, 8, 8);
                    border-radius: 40%;
                  }
                  img:hover{
                    border: 2px solid rgba(255, 255, 255, 0.5);
                    cursor: pointer;
                    
                  }  
                }
                input{
                  border: 0px ;
                  outline: none;
                  width: 23vw;
                  background-color: rgba(0, 0, 0, 0);
                  border-bottom: 2px solid rgba(255, 255, 255, 0.5);
                  height: 4vh;
                  color: white;
                  transition: 0.5s ease all;
                  &:focus{
                    border-bottom: 2px solid rgba(255, 255, 255, 1);
                  }
                }
              }

            }
            .chatElement{
              &:hover{
                background-color: rgb(8, 8, 8);
                cursor: pointer;
              }
              background-color: rgba(8, 8, 8, 0.8);
              transition: 0.3s all ease;
              // margin-top: 5px;
              height: 15vh;
              border-bottom: 2px solid rgb(42, 42, 42);
              display: flex;
                align-items: center;
              &-con{
                display: flex;
                align-items: center;
                &__ava{
                  display: block;
                  width: 80px;
                  margin-left: 10px;
                  border-radius: 40%;
                  border: 2px solid transparent;
                  transition: 0.3s all ease;
                  &:hover{
                    border: 2px solid rgba(208, 208, 8, 1);
                  }
                }
                &__text{
                  margin-left: 20px;
                  margin-right: 10px;
                  .title{
                    font-size: 20px;
                    margin-bottom: 5px;
                    transition: 0.3s all ease;
                    
                  }
                  .text{
                    max-height: 20px;
                    overflow: hidden;
                    font-family: Mont;

                  }
                }
              }
            }
          }
        }
        .messageHistory{
          margin-left: 5px;
          // margin-top: 5px;
          background-color: rgb(8, 8, 8);
          position: fixed;
          height: 100vh;
          width: 58.9vw;
          right: 5.5vw;
          z-index: 100;
          .messageFooter{
            position: fixed;
            bottom: 0px;
            right: 40px;
            width: 58.9vw;
            min-height: 10vh;
            right: 5.5vw;
            border-bottom: 5px solid rgb(42, 42, 42);
            background-color: rgb(8,8,8);
            border-top: 5px solid rgb(42, 42, 42);
            display: flex;
            align-items: center;
            button{
              outline: none;
              border: none;
              background-color: transparent;
              img{
                width: 35px;
                border: 2px solid rgba(255, 255, 255, 0);
                transition: 0.3s all ease;
                border-radius: 40%;
              }
              img:hover{
                border: 2px solid rgba(255, 255, 255, 0.5);
                cursor: pointer;
              }
            }
            textarea{
              background-color: transparent;
              resize: none;
              outline: none;
              border: none;
              color: white;
              font-size: 15px;
              width: 42vw;
              height: 3vh !important;
              border-bottom: 2px solid rgba(255, 255, 255, 0.5);
              padding-bottom: 8px;
              &:focus{
                border-bottom: 2px solid rgba(255, 255, 255, 1);
              }
            }
          }
          .messages-container{
            display: grid;
            grid-template-columns: 1fr;
            &__my{
              display: flex;
              justify-content: flex-start;
              .myMessages{
                border-bottom: 2px solid rgba(255, 255, 255, 0.5);
                padding: 10px 0;
                margin: 5px 20px;
                max-width: 50%;
              }
            }
            &__your{
              display: flex;
            justify-content: flex-end;
              .yourMessages{
                color: rgba(208, 208, 8, 1);
                border-bottom: 2px solid rgba(208, 208, 8, 0.5);
                padding: 10px 0;
                margin: 5px 20px;
                max-width: 50%;
              }
            }
          }
          
        }
      }
    }
    .users{
      background-color: rgb(12, 12, 12);
      margin-left: -8px;
      &-con{
        padding: 15px 15px 5px 15px;
        max-width: 50vw;
        display: flex;
        // flex-direction: column;
        align-items: center;
        border-right: 2px solid rgb(42, 42, 42);
        border-bottom: 2px solid rgb(42, 42, 42);
        &__left{
          display: flex;
          align-items: center;
          flex-direction: column;
          .img{
            img{
              width: 100px;
              height: 100px;
              object-fit: cover;
              border-radius: 40%;
              border: 2px solid transparent;
              transition: 0.5s all ease;
              &:hover{
              border: 2px solid rgba(208, 208, 8, 1);
              cursor: pointer;
              }
            }
          }
          .btn{
            button{
              outline: none;
              width: 100px;
              background-color: rgba(0, 0, 0, 0);
              border: 2px solid rgba(255, 255, 255, 0);
              height: 32px;
              color: rgba(255, 255, 255, 0.733);
              font-family: MontR;
              text-transform:lowercase;
              font-size: 16px;
              letter-spacing: 2px;
              padding: auto;
              text-align: center;
              // padding-left: 15px;
              border-bottom: 2px solid rgba(0, 0, 0, 0);
              transition: 0.5s all ease;
            }
            button:hover{
              border-bottom: 2px solid rgba(255, 255, 255, 0.5);
              cursor: pointer;
            }
          }
        }
        &__right{
          display: flex;
          min-height: 125px;
          margin: 10px 20px;
          .con{
            min-width: 450px;
            max-width: 475px;
            .fullName{
              font-family: PAS;
              font-size: 50px;
              color: rgb(100, 100, 100);
              text-transform: uppercase;
              transition: 0.5s all ease;
              &:hover{
                color: rgba(255, 255, 255, 0.8);
                cursor: pointer;
              }
            }
            .status{
              font-family: MontR;
              overflow: hidden;
              max-width: 450px;
            }
          }
        }
          .location{
            display: flex;
            justify-self: end;
            // justify-content: flex-end;
            align-items: flex-end;
            flex-direction: column;
            text-align: center;
            width: 100px;
            font-family: MontB;
            color: rgb(100, 100, 100);
            line-height: 25px;
            font-size: 20px;
            .city{
              transition: 0.3s all ease;
              &:hover{
                color: rgba(255, 255, 255, 0.8);
                cursor: pointer;
              }
            }
            .country{
              transition: 0.3s all ease;
              &:hover{
                color: rgba(255, 255, 255, 0.8);
                cursor: pointer;
              }
            }
          }
        
      }
    }
  }
  
